declare var wx;
declare var tt;
import { _decorator, Component, Node, TerrainLayer } from 'cc';
import { Global } from '../Global';
const { ccclass, property } = _decorator;

@ccclass('AdMgr')
/**
 * 广告管理
 */
export class AdMgr extends Component {

    //
    private static _instance: AdMgr = null;
    //
    public static get instance() {
        if (!this._instance) {
            this._instance = new AdMgr();
        }
        return this._instance;
    }

    //回调
    private Cb: Function | null = null;

    // //微信广告配置
    // private WxAd = {
    //     VideoInfor: { ad: null, id: "adunit-cd68f27c803028bf" },//id是广告id
    //     BannerInfor: { ad: null, id: "adunit-a4d23d6becd2991e" },//id是广告id
    //     CustomAd: { ad: null, id: "adunit-f0ae5d3a8f87d142" }
    // }
    //微信广告配置
    private WxAd = {
        VideoInfor: { ad: null, id: null },//id是广告id
        BannerInfor: { ad: null, id: null },//id是广告id
        CustomAd: { ad: null, id: null }
    }

    //抖音广告配置
    private DyAd = {
        VideoInfor: { ad: null, id: "3ajmhl5le9b39hm8kq" },//id是广告id
        BannerInfor: { ad: null, id: null }//id是广告id
    }

    //视频广告是否加载完成
    private IsVideoComplete: boolean = false;

    /**
     * 初始化
     */
    public Init() {
        //微信环境中
        if (Global.IsWx) this.WxInit();
        //抖音环境中
        if (Global.IsDy) this.DyInit();
    }

    /**
     * 微信广告初始化
     */
    public WxInit() {
        //视频广告
        if (this.WxAd.VideoInfor.id) {
            this.WxAd.VideoInfor.ad = wx.createRewardedVideoAd({ adUnitId: this.WxAd.VideoInfor.id });
            this.WxAd.VideoInfor.ad.onLoad(this.VideoComplete.bind(this));
            this.WxAd.VideoInfor.ad.onError((err) => { Global.IsDebug && console.log("激励广告调用失败", err) });
            this.WxAd.VideoInfor.ad.onClose((e) => { this.VideoClose(e.isEnded); });
        }
        //banner广告
        if (this.WxAd.BannerInfor.id) {
            this.WxAd.BannerInfor.ad = wx.createBannerAd({
                adUnitId: this.WxAd.BannerInfor.id,
                adIntervals: 30,
                style: {
                    left: 0,
                    top: 0,
                    width: screen.width
                }
            })
            this.WxAd.BannerInfor.ad.onError((err) => { Global.IsDebug && console.log("Banner广告拉去失败", err) })
            this.WxAd.BannerInfor.ad.onLoad(() => { Global.IsDebug && console.log("Banner广告加载成功") })

            this.WxAd.BannerInfor.ad.onResize((res) => {
                this.WxAd.BannerInfor.ad.top = screen.height - res.height;
            })
        }
        //原生广告
        if (this.WxAd.CustomAd.id) {
            this.WxAd.CustomAd.ad = wx.createCustomAd({
                adUnitId: this.WxAd.CustomAd.id,
                adIntervals: 30,
                style: {
                    left: 0,
                    top: screen.height / 2 - 200,
                }
            })
        }
    }

    /**
     * 抖音广告初始化
     */
    public DyInit() {

        tt.onShow((res) => {
            console.log("抖音场景", res);
            Global.SceneString = (res.scene == "021036");
            console.log("抖音场景1", Global.SceneString);
        })

        if (this.DyAd.VideoInfor.id) {
            this.DyAd.VideoInfor.ad = tt.createRewardedVideoAd({ adUnitId: this.DyAd.VideoInfor.id });
            this.DyAd.VideoInfor.ad.onLoad(this.VideoComplete.bind(this));
            this.DyAd.VideoInfor.ad.load();
            this.DyAd.VideoInfor.ad.onClose((e) => { this.VideoClose(e.isEnded); })
        }
    }

    /**
     * 显示banner广告
     * @param 是否显示
     */
    public ShowBanner(_isshow) {
        if (Global.IsWx && this.WxAd.BannerInfor.id) {
            if (_isshow) {
                this.WxAd.BannerInfor.ad.show();
                this.WxAd.BannerInfor.ad.style.top = screen.height - this.WxAd.BannerInfor.ad.style.realHeight;
            }
            else this.WxAd.BannerInfor.ad.hide();
        }
    }

    /**
     * 显示原生模板广告
     */
    public ShowCustom(_isshow) {
        if (Global.IsWx && this.WxAd.CustomAd.id) {
            if (_isshow) {
                this.WxAd.CustomAd.ad.show();
            }
            else this.WxAd.CustomAd.ad.hide();
        }
    }

    /**
     * 显示视频广告
     * @param 回调函数
     */
    public ShowVideo(_cb: Function) {
        this.Cb = _cb;
        if (Global.IsWx && this.WxAd.VideoInfor.ad) {
            //播放广告
            this.WxAd.VideoInfor.ad.show().catch(() => {
                //显示失败了重新加载
                this.WxAd.VideoInfor.ad.load().then(() => {
                    this.WxAd.VideoInfor.ad.show().catch((err) => {
                        //显示失败了重新加载
                        Global.IsDebug && console.log('视频广告加载失败', err);
                        this.VideoClose(false);
                    })
                })
            })
            return;
        }

        if (Global.IsDy && this.DyAd.VideoInfor.ad) {
            //播放广告
            this.DyAd.VideoInfor.ad.show().catch(() => {
                //显示失败了重新加载
                this.DyAd.VideoInfor.ad.load().then(() => {
                    this.DyAd.VideoInfor.ad.show().catch((err) => {
                        //显示失败了重新加载
                        Global.IsDebug && console.log('视频广告加载失败', err);
                        this.VideoClose(false);
                    })
                })
            })
            return;
        }

        this.VideoClose(true);
    }


    /**
     * 激励广告点击关闭按钮
     * @param 是否在完整观看得情况下关闭
     */
    public VideoClose(_isended: boolean) {
        console.log(_isended, "用户看完了么")
        if (Global.IsDy) {
            tt.showToast({
                title: _isended ? "领取成功" : "领取失败",
                icon: _isended ? "success" : "fail"
            })
        }
        this.Cb && this.Cb(_isended);
    }

    /**
     * 视频广告加载完成
     */
    private VideoComplete() {
        Global.IsDebug && console.log("广告加载完成");
        this.IsVideoComplete = true;
    }

}


