import { _decorator, Component, Label, Node } from 'cc';
import { MessageCenter } from '../../Message/MessageCenter';
import { MessageType } from '../../Message/Message';
import { AudioMgr } from '../../Mgr/AudioMgr';
const { ccclass, property } = _decorator;

@ccclass('PopInstru')
export class PopInstru extends Component {

    protected start(): void {
        this.node.getChildByName("Content").getComponent(Label).string = "        身为一名猫咪小吃铺的老板，需要根据顾客的需求制作出符合他们口味的小吃，他们会把小吃的口味分享到世界各地，引来更多的猫咪品尝，快来开启你的猫咪小吃铺之旅吧！"
    }

    /**
      * 点击关闭按钮
      */
    private CloseClick() {
        AudioMgr.instance.PlayButton();
        MessageCenter.SendMessage(
            MessageType.Type_Top,
            MessageType.Top_HideAll,
            null
        )
    }

}


